.form {
  &__input {
    width: 100%;
    border: none;
    border-bottom: 1px solid $colorChocolate;
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 24px;
  }

  &__input:nth-child(3) {
    margin-bottom: 90px;
  }

  &__input:nth-child(4) {
    margin-bottom: 30px;

    @include onDesktop {
      margin-bottom: 37px;
    }
  }

  &__textarea {
    height: 26px;
    resize: none;
  }

  &__button {
    width: 160px;
  }
}
