.page {
  font-family: "Rubik", sans-serif;
  box-sizing: border-box;
  color: $colorChocolate;
  font-size: 16px;
  line-height: 24px;
  scroll-behavior: smooth;

  &__section {
    padding: 40px 16px;

    @include onDesktop {
      padding: 40px 60px;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;

    z-index: 1;

    transform: translateX(100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
